import { atom } from "jotai";
import { atomWithStorage } from "jotai/utils";
import slugify from "slugify";

/* Atom para salvar as respostas que o usurio submeter nos pr-testes.*/
export const preTesteRespostasAtom = atomWithStorage(
  "respostasPreTestePorLicao",
  {}
);

/* Tipagem para os argumentos da funo Write de editarRespostasAtom */
export type EditarRespostasAction = {
  lessonTitle: string;
  resposta: string;
};

/* Atom para salvar a resposta do usurio no localStorage.
A ideia  que a resposta s seja checada depois do usurio assistir  lio.
Como a inteno  que o progresso do usurio tambm fique salvo em forma de cookie (j que o site  esttico e no h backend), ento o localStorage serve aqui simultaneamente como global state e database */
export const editarRespostaPreTesteAtom = atom(
  // Pra fazer: testar se d para trocar isto por null
  (get: any) => (lessonTitle: string) =>
    get(preTesteRespostasAtom)[slugify(lessonTitle)],
  (get: any, set: any, { lessonTitle, resposta }: EditarRespostasAction) => {
    let respostas = {
      ...get(preTesteRespostasAtom),
      ...{ [slugify(lessonTitle, { lower: true })]: resposta },
    };
    set(preTesteRespostasAtom, respostas);
  }
);
